"use client";

import { useState, useEffect, useMemo, useRef } from "react";
import { useParams, useRouter } from "next/navigation";
import { getAdById, Ad } from "@/lib/ads";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Button } from "@/components/ui/button";
import { useToast } from "@/hooks/use-toast";
import { useRewards } from "@/hooks/use-rewards";
import { ArrowLeft, PlayCircle, AlertTriangle, PauseCircle } from "lucide-react";
import { verifyAdView } from "./actions";

export default function AdViewerPage() {
  const [ad, setAd] = useState<Ad | null>(null);
  const [progress, setProgress] = useState(0);
  const [isWatching, setIsWatching] = useState(false);
  const [isClaiming, setIsClaiming] = useState(false);
  const [error, setError] = useState<string | null>(null);
  
  const params = useParams();
  const router = useRouter();
  const { toast } = useToast();
  const { addReward } = useRewards();
  const videoRef = useRef<HTMLVideoElement>(null);

  const adId = useMemo(() => Array.isArray(params.id) ? params.id[0] : params.id, [params.id]);

  useEffect(() => {
    if (adId) {
      const foundAd = getAdById(adId);
      if (foundAd) {
        setAd(foundAd);
      } else {
        setError("Ad not found.");
      }
    }
  }, [adId]);

  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (isWatching && ad && progress < 100) {
      timer = setInterval(() => {
        setProgress((prev) => {
          const newProgress = prev + (100 / ad.duration);
          if (newProgress >= 100) {
            setIsWatching(false);
            if(videoRef.current) {
              videoRef.current.pause();
            }
            return 100;
          }
          return newProgress;
        });
      }, 1000);
    }
    return () => clearInterval(timer);
  }, [isWatching, ad, progress]);

  const handlePlayPause = () => {
    if (videoRef.current) {
      if (videoRef.current.paused) {
        videoRef.current.play();
      } else {
        videoRef.current.pause();
      }
    }
  };

  const handleClaimReward = async () => {
    if (!ad) return;
    setIsClaiming(true);
    
    const result = await verifyAdView(ad);

    if (result.success) {
      addReward(result.reward);
      toast({
        title: "Reward Claimed!",
        description: `You've earned $${result.reward.toFixed(2)}.`,
      });
      router.push("/dashboard");
    } else {
      toast({
        variant: "destructive",
        title: "Claim Failed",
        description: result.reason,
      });
      setIsClaiming(false);
    }
  };

  if (error) {
    return (
      <Card className="max-w-2xl mx-auto">
        <CardHeader>
          <CardTitle className="text-destructive flex items-center gap-2"><AlertTriangle /> Error</CardTitle>
        </CardHeader>
        <CardContent>
          <p>{error}</p>
          <Button onClick={() => router.push('/dashboard')} variant="link" className="mt-4"><ArrowLeft className="mr-2 h-4 w-4"/>Back to Dashboard</Button>
        </CardContent>
      </Card>
    );
  }

  if (!ad) {
    return <div>Loading ad...</div>;
  }

  const isComplete = progress >= 100;

  return (
    <div className="max-w-4xl mx-auto">
      <Button onClick={() => router.back()} variant="ghost" className="mb-4">
        <ArrowLeft className="mr-2 h-4 w-4" /> Back
      </Button>
      <Card>
        <CardHeader>
          <CardTitle>{ad.title}</CardTitle>
          <CardDescription>{ad.description}</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="aspect-video bg-slate-800 rounded-lg overflow-hidden relative mb-4">
            <video
              ref={videoRef}
              key={ad.videoUrl}
              className="w-full h-full"
              onPlay={() => setIsWatching(true)}
              onPause={() => setIsWatching(false)}
              onEnded={() => {
                setIsWatching(false);
                setProgress(100);
              }}
              controls={isComplete}
            >
              <source src={ad.videoUrl} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
            {!isComplete && (
                 <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-50 transition-opacity opacity-100 hover:opacity-0 cursor-pointer" onClick={handlePlayPause}>
                    {isWatching 
                      ? <PauseCircle className="w-24 h-24 text-white opacity-80" />
                      : <PlayCircle className="w-24 h-24 text-white opacity-80" />
                    }
                </div>
            )}
          </div>

          <div className="space-y-2 mb-4">
            <p className="text-sm font-medium">Watch for {ad.duration} seconds to claim your reward.</p>
            <Progress value={progress} />
            <p className="text-xs text-muted-foreground text-right">{Math.min(Math.floor((progress/100) * ad.duration), ad.duration)}s / {ad.duration}s</p>
          </div>
          
          <Button 
            onClick={handleClaimReward} 
            disabled={!isComplete || isClaiming}
            className="w-full"
            size="lg"
          >
            {isClaiming ? 'Verifying...' : 'Claim Reward'}
          </Button>
        </CardContent>
      </Card>
    </div>
  );
}
